.kokopu-coordinate {
	fill: currentcolor;
	font-style: normal;
	font-family: sans-serif;
	text-anchor: middle;
	dominant-baseline: central;
	letter-spacing: 0px;
}

.kokopu-annotation {
	opacity: 0.8;
}

.kokopu-hoveredSquare,
.kokopu-handle {
	fill: transparent;
}

.kokopu-clickable {
	cursor: pointer;
}

.kokopu-pieceDraggable {
	cursor: grab;
}

.kokopu-pieceDraggable.kokopu-dragging {
	cursor: grabbing;
}

.kokopu-arrowDraggable {
	cursor: crosshair;
}

.kokopu-drawerMask {
	opacity: 0.5;
}
