.kokopu-errorBox {
	text-align: left;
	font-family: sans-serif;
	font-size: 12px;
	line-height: 1.3em;
	color: #800;
	background-color: #fdd;
	border: solid 1px #c00;
	padding: 0.75em;
}

.kokopu-errorTitle {
	font-weight: bold;
	font-style: italic;
}

.kokopu-errorMessage,
.kokopu-errorExcerpt {
	margin-top: 0.4em;
}

.kokopu-errorExcerpt {
	white-space: pre-wrap;
	font-family: monospace;
	line-height: 1.5em;
	color: #400;
	background-color: #ffd;
	border: solid 1px #aa0;
	padding: 0.75em 1em 0.15em;
}
